import React, { useCallback, useState, lazy, Suspense } from 'react';
import {
  Button,
  TextInput,
  NumberInput,
  Select,
  SelectItem,
  Checkbox,
  DataTable,
  TableRow,
  TableHead,
  TableHeader,
  TableContainer,
  TableCell,
  Table,
  TableBody,
  TableToolbar,
  TableToolbarContent,
} from 'carbon-components-react';

import Collapsible from 'react-collapsible';
import { Tabs, Tab, TabPanel, TabList } from 'react-web-tabs';
import ReactTooltip from "react-tooltip";

import { safeLoad, getDivHeight, jsonToYaml, updateStyle, updateStyle2, validateField, toggleDescription } from '../../helpers';


const PoliciescontentInjectionpathsDeleteModal = lazy(() => import('./policiescontent_injectionpathsDeleteModal'));
const PoliciescontentInjectionpathsAdd = lazy(() => import('./policiescontent_injectionpathsAdd'));
const PoliciescontentInjectionpathsEdit = lazy(() => import('./policiescontent_injectionpathsEdit'));


  const handleChange = () => {
  };

  const handleSave = (loadGrid, idPrefix, doClose) => {

    if(idPrefix === undefined) idPrefix = "";

    var json = {};
      
    if(document.getElementById("policies_content_injection_name_id" + idPrefix).value.endsWith(":") !== true) {
      json["policies_content_injection_name"] = document.getElementById("policies_content_injection_name_id" + idPrefix).value;
    }
    json["policies_content_injection_full_line_match"] = document.getElementById("policies_content_injection_full_line_match_id" + idPrefix).checked.toString();
    if(document.getElementById("policies_content_injection_location_id" + idPrefix).value.endsWith(":") !== true) {
      json["policies_content_injection_location"] = document.getElementById("policies_content_injection_location_id" + idPrefix).value;
    }
    json["policies_content_injection_replace_match"] = document.getElementById("policies_content_injection_replace_match_id" + idPrefix).checked.toString();
    if(document.getElementById("policies_content_injection_content_id" + idPrefix).value.endsWith(":") !== true) {
      json["policies_content_injection_content"] = document.getElementById("policies_content_injection_content_id" + idPrefix).value;
    }
    json["policies_content_injection_paths"] = policies_content_injection_pathsrowData.policies_content_injection_paths;
      
    loadGrid( json["policies_content_injection_name"], json["policies_content_injection_full_line_match"], json["policies_content_injection_location"], json["policies_content_injection_replace_match"], json["policies_content_injection_content"], json["policies_content_injection_paths"], true); 

    handleClose(idPrefix, doClose);
  };

  const handleAdd = (setpolicies_content_injection_paths, idPrefix, doAdd, doClose) => {
    if(idPrefix === undefined) idPrefix = "";


    document.getElementById("policies_content_injection_name_id").value = "";
    document.getElementById("policies_content_injection_full_line_match_id").checked = false;
    document.getElementById("policies_content_injection_location_id").value = "";
    document.getElementById("policies_content_injection_replace_match_id").checked = false;
    document.getElementById("policies_content_injection_content_id").value = "";
    policies_content_injection_pathsrowData.policies_content_injection_paths = []
    setpolicies_content_injection_paths(policies_content_injection_pathsrowData.policies_content_injection_paths);


    if(doAdd) {
      doAdd();
    }

    handleClose(idPrefix,  doClose);
  };

  const handleClose = (idPrefix, doClose) => {
    updateStyle("policies_div" + idPrefix, "display", "block");
    document.getElementById("addpolicies_content_injectionPage" + idPrefix).style.display = "none";

      if(doClose) {
        doClose();
      }
  };

  const closeAdd = (idPrefix) => {
      if(idPrefix === undefined) idPrefix = "";

      updateStyle("policies_content_injection_div" + idPrefix, "display", "block");
      document.getElementById("policies_content_injection_tab" + idPrefix).style.display = "flex";

  };

const policies_content_injection_pathsheaderData = [
    {
    header: 'Path',
    key: 'paths',
  },

];

const policies_content_injection_pathsrowData = {policies_content_injection_paths: []};

const policies_content_injection_pathshandleAdd = (idPrefix, clearFields) => {
    if(idPrefix === undefined) idPrefix = "";

    // Set the fields to empty
    if(clearFields) {
      for(var i = 0; i < clearFields.length; i++) {
        eval(clearFields[i]);
      }
    }

    document.getElementById("policies_content_injection_paths_paths_id" + idPrefix).value = "";
    // Hide the tab page
    updateStyle("policies_content_injection_div" + idPrefix, "display", "none");
    // Show the add page
    document.getElementById("addpoliciescontent_injection_pathsPage" + idPrefix).style.display = "block";
    // Hide the remaining fields
    document.getElementById("policies_content_injection_tab" + idPrefix).style.display = "none";

  };

const Policies_content_injectionAdd = (props) => {

    var row = props.row;
    const loadGrid = props.loadGrid;
    const doClose = props.doClose;
    const idPrefix = (props.idPrefix)?props.idPrefix:"";

    const [policies_content_injection_paths, setpolicies_content_injection_paths] = React.useState([]);
    const [fetchedpolicies_content_injection_paths, setFetchedpolicies_content_injection_paths] = React.useState(false);

    React.useEffect(() => {
      policies_content_injection_pathsloadGrid();
    }, [fetchedpolicies_content_injection_paths]);

    const policies_content_injection_pathsloadGrid = React.useCallback(async (policies_content_injection_paths_paths, add) => {
      try {
        var attrs = [];
        if(policies_content_injection_paths_paths) {
          if(add) {
            for(var i = 0; i < policies_content_injection_pathsrowData.policies_content_injection_paths.length; i++) {
              var tempId = ((policies_content_injection_pathsrowData.policies_content_injection_paths[i].paths)?policies_content_injection_pathsrowData.policies_content_injection_paths[i].paths:"") + "";
              if((window.policies_content_injection_pathsediting != true && window.policies_content_injection_pathsediting != "true") || tempId != window.policies_content_injection_pathsrowId) {
                attrs.push(policies_content_injection_pathsrowData.policies_content_injection_paths[i]);
              }
            }
            var newId = ((policies_content_injection_paths_paths)?policies_content_injection_paths_paths:"") + "";
            attrs.push({
              id: newId,
              paths: policies_content_injection_paths_paths,
            });
          } else {
            // Must be a delete
            for(var i = 0; i < policies_content_injection_pathsrowData.policies_content_injection_paths.length; i++) {
              if(policies_content_injection_pathsrowData.policies_content_injection_paths[i].paths !== policies_content_injection_paths_paths ) {
                attrs.push(policies_content_injection_pathsrowData.policies_content_injection_paths[i]);
              }
            }
          }
        } else {
          attrs = policies_content_injection_pathsrowData.policies_content_injection_paths;
        }
        policies_content_injection_pathsrowData.policies_content_injection_paths = attrs;
        const data = {policies_content_injection_paths: attrs};
        setpolicies_content_injection_paths(data.policies_content_injection_paths);

        if(policies_content_injection_paths_paths !== undefined && policies_content_injection_paths_paths !== null ) {
            handleChange();

        }

      } catch (e) {
        console.log(e);
      }
      return true;
    });



    const [policies_content_injection_name, setpolicies_content_injection_name] = useState(''),
    updatepolicies_content_injection_name = useCallback((evt) => setpolicies_content_injection_name(evt.target.value), []);

    const [policies_content_injection_paths_paths, setpolicies_content_injection_paths_paths] = useState(''),
    updatepolicies_content_injection_paths_paths = useCallback((evt) => {
      setpolicies_content_injection_paths_paths(evt.target.value);
      var tempId = evt.target.value;
      validateField(policies_content_injection_pathsrowData.policies_content_injection_paths, setpolicies_content_injection_paths_paths_valid, setpolicies_content_injection_paths_paths_invalid_text, "Policies Content Injection Paths", "paths", tempId);
    }, []);

    const [policies_content_injection_paths_paths_valid, setpolicies_content_injection_paths_paths_valid] = useState(true);
    const [policies_content_injection_paths_paths_invalid_text, setpolicies_content_injection_paths_paths_invalid_text] = useState("");


    const [policies_content_injection_full_line_match, setpolicies_content_injection_full_line_match] = useState(true),
    updatepolicies_content_injection_full_line_match = useCallback((evt) => setpolicies_content_injection_full_line_match(evt), []);

    const [policies_content_injection_location, setpolicies_content_injection_location] = useState(''),
    updatepolicies_content_injection_location = useCallback((evt) => setpolicies_content_injection_location(evt.target.value), []);

    const [policies_content_injection_replace_match, setpolicies_content_injection_replace_match] = useState(false),
    updatepolicies_content_injection_replace_match = useCallback((evt) => setpolicies_content_injection_replace_match(evt), []);

    const [policies_content_injection_content, setpolicies_content_injection_content] = useState(''),
    updatepolicies_content_injection_content = useCallback((evt) => setpolicies_content_injection_content(evt.target.value), []);


    const [fetcheddata, setFetcheddata] = React.useState(false);
    React.useEffect(() => {
      dataLoad();
    }, [fetcheddata]);
    const dataLoad = () => {

      for(var i = 0; i < policies_content_injection_pathsrowData.policies_content_injection_paths.length; i++) {
        if(policies_content_injection_pathsrowData.policies_content_injection_paths[i].id === window.policies_content_injection_pathsrowId) {
        setpolicies_content_injection_paths_paths((policies_content_injection_pathsrowData.policies_content_injection_paths[i] !== undefined && policies_content_injection_pathsrowData.policies_content_injection_paths[i].paths !== undefined )?policies_content_injection_pathsrowData.policies_content_injection_paths[i].paths:"");
        }
      }


      return true;
    };

    const setDataMain = (functionName, data) => {
      if(functionName && data) {
        eval(functionName)(data);
      }
    };

    return (
      <div>
        
        <div id={'addpolicies_content_injectionPage' + idPrefix} style={{display:'none'}} className="bx--col bx--no-gutter">

          <Tabs id= {"policies_content_injection_tab" + idPrefix} defaultTab={"General_tab" + idPrefix} vertical className="vertical-tabs" style={{overflow:'auto'}}>
              <TabList>
                <Tab tabFor={"General_tab" + idPrefix}>General</Tab>
                <Tab tabFor={"paths_tab" + idPrefix}>Paths</Tab>
              </TabList>
              <TabPanel style={{width:'100%', overflow:'auto'}} tabId={"General_tab" + idPrefix}>
                <div className="iag_sublevel_tabs" style={{height:'60vh', overflow:'auto'}}>
                <div className= {"iag_form_elem iag_tab_heading policies_content_injection_div" + idPrefix}>
                  <span className="iag_breadcrumb">Policies / Content Injection : </span><br/>                  <h2>General Settings</h2><hr/>
                  Specifies the general settings associated with this component
                </div>
                <div className={"iag--simple-div policies_content_injection_div" + idPrefix}>
                <div className= {"iag_form_elem policies_content_injection_div" + idPrefix}>
          <TextInput  invalid={!props.policies_content_injection_name_valid}  invalidText={props.policies_content_injection_name_invalid_text}  value={props.policies_content_injection_name}  id={"policies_content_injection_name_id" + idPrefix}  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"policies_content_injection_name_tooltip_id" + idPrefix}  labelText="Name *" onChange={props.updatepolicies_content_injection_name}/>
          <ReactTooltip id={"policies_content_injection_name_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>A name to be associated with this content injection rule.<br/></div></ReactTooltip>
        </div>

                <div className= {"iag_form_elem policies_content_injection_div" + idPrefix}>
          <Checkbox checked={props.policies_content_injection_full_line_match} id={"policies_content_injection_full_line_match_id" + idPrefix}  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"policies_content_injection_full_line_match_tooltip_id" + idPrefix}  labelText="Full Line Match" onChange={props.updatepolicies_content_injection_full_line_match}/>
          <ReactTooltip id={"policies_content_injection_full_line_match_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>Should the location match a full line, or should it match any string in the response?  When a full line match is configured the content will be inserted prior to the matching line.  When a partial line match is configured the content will be inserted immediately prior to the matching string.<br/></div></ReactTooltip>
        </div>

                <div className= {"iag_form_elem policies_content_injection_div" + idPrefix}>
          <TextInput  value={props.policies_content_injection_location}  id={"policies_content_injection_location_id" + idPrefix}  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"policies_content_injection_location_tooltip_id" + idPrefix}  labelText="Location" onChange={props.updatepolicies_content_injection_location}/>
          <ReactTooltip id={"policies_content_injection_location_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The location where the content should be injected. If a full line match is being used the location is pattern matched against a line in the response using the `*.` wildcard characters.  The maximum length of a line which can be matched is 8192 bytes.<br/></div></ReactTooltip>
        </div>

                <div className= {"iag_form_elem policies_content_injection_div" + idPrefix}>
          <Checkbox checked={props.policies_content_injection_replace_match} id={"policies_content_injection_replace_match_id" + idPrefix}  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"policies_content_injection_replace_match_tooltip_id" + idPrefix}  labelText="Replace Match" onChange={props.updatepolicies_content_injection_replace_match}/>
          <ReactTooltip id={"policies_content_injection_replace_match_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>If a partial line match is being used this configuration entry will control whether the matching string is replaced with the supplied content, or whether the supplied content is inserted prior to the matching string.  This configuration entry will be ignored if full line matches are being used.<br/></div></ReactTooltip>
        </div>

                <div className= {"iag_form_elem policies_content_injection_div" + idPrefix}>
          <TextInput  value={props.policies_content_injection_content}  id={"policies_content_injection_content_id" + idPrefix}  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"policies_content_injection_content_tooltip_id" + idPrefix}  labelText="Content" onChange={props.updatepolicies_content_injection_content}/>
          <ReactTooltip id={"policies_content_injection_content_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The data which is to be injected.<br/></div></ReactTooltip>
        </div>

                </div>
                </div>
                  <div className= {"iag_form_elem policies_content_injection_div" + idPrefix}>
                    <Button onClick={() => { handleClose(idPrefix,  doClose); }}>Cancel</Button>
                    <Button disabled={(!props.policies_content_injection_name || !props.policies_content_injection_name_valid)} onClick={() => { handleSave(loadGrid, idPrefix,  doClose); }}>Save</Button>
                  </div>
              </TabPanel>
              <TabPanel style={{width:'100%', overflow:'auto'}} tabId={"paths_tab" + idPrefix}>
                <div className="iag_sublevel_tabs" style={{height:'60vh', overflow:'auto'}}>
                <div className= {"iag_form_elem iag_tab_heading policies_content_injection_div" + idPrefix}>
                  <span className="iag_breadcrumb">Policies / Content Injection : </span><br/>                  <h2>Paths Settings</h2><hr/>
                </div>
                <div className={"iag--simple-div policies_content_injection_div" + idPrefix}>
                <div className= {"iag_form_elem policies_content_injection_div" + idPrefix}>
  <h3 tabIndex="0"  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"policies_content_injection_paths_tooltip_id" + idPrefix} >Paths</h3><hr/>
</div>
          <ReactTooltip id={"policies_content_injection_paths_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The path for which content injection will take place. This entry is an array and can be used to specify multiple paths.<br/></div></ReactTooltip>

                <div id={"policies_content_injection_pathsTableDiv" + idPrefix} style={{marginLeft:"10px",marginBottom:"10px"}}>
                  <div className={"iag_form_elem policies_content_injection_div" + idPrefix}>
                    <DataTable id={"policies_content_injection_pathsTable" + idPrefix} isSortable
                        headers={policies_content_injection_pathsheaderData}
                        rows={policies_content_injection_pathsrowData.policies_content_injection_paths}
                        render={({
                          rows,
                          headers,
                          getHeaderProps,
                          getRowProps,
                          getTableProps,
                          getToolbarProps,
                          onInputChange,
                          getTableContainerProps
                    }) => (
                      <TableContainer>
                        <TableToolbar aria-label="data table toolbar">
                          <TableToolbarContent>
                            <Button onClick={() => {policies_content_injection_pathshandleAdd(idPrefix, [setpolicies_content_injection_paths_paths(''),    setpolicies_content_injection_paths_paths_valid(true),]);}}>Add</Button>
                          </TableToolbarContent>
                        </TableToolbar>
                        <Table size='normal' {...getTableProps()}>
                          <TableHead>
                            <TableRow>
                              {headers.map(header => (
                                <TableHeader {...getHeaderProps({ header })}>
                                  {header.header}
                                </TableHeader>
                              ))}
                              <TableHeader>
                                  Actions
                              </TableHeader>
                            </TableRow>
                          </TableHead>
                          <TableBody>
                            {rows.map(row => (
                              <TableRow key={row.id}>
                                {row.cells.map(cell => (
                                  <TableCell key={cell.id}>{cell.value}</TableCell>
                                ))}
                                <TableCell>
                                  <Suspense fallback={<div></div>}>
                                    <PoliciescontentInjectionpathsDeleteModal row={row} loadGrid={policies_content_injection_pathsloadGrid} />
                                  </Suspense>
                                  <Suspense fallback={<div></div>}>
                                    <PoliciescontentInjectionpathsEdit  data={policies_content_injection_pathsrowData.policies_content_injection_paths} setData={setDataMain} row={row} loadGrid={policies_content_injection_pathsloadGrid} dataLoad={dataLoad} editing={true} name={"policies_content_injection_paths"} idPrefix={idPrefix + "_add"} parentIdPrefix={idPrefix} 
            updatepolicies_content_injection_paths_paths={updatepolicies_content_injection_paths_paths} policies_content_injection_paths_paths={policies_content_injection_paths_paths}
            policies_content_injection_paths_paths_valid={policies_content_injection_paths_paths_valid}
            policies_content_injection_paths_paths_invalid_text={policies_content_injection_paths_paths_invalid_text}
                                    />
                                  </Suspense>
                                </TableCell>
                              </TableRow>
                            ))}
                          </TableBody>
                        </Table>
                      </TableContainer>)}
                    />
                  </div>
                </div>

                </div>
                </div>
                  <div className= {"iag_form_elem policies_content_injection_div" + idPrefix}>
                    <Button onClick={() => { handleClose(idPrefix,  doClose); }}>Cancel</Button>
                    <Button disabled={(!props.policies_content_injection_name || !props.policies_content_injection_name_valid)} onClick={() => { handleSave(loadGrid, idPrefix,  doClose); }}>Save</Button>
                  </div>
              </TabPanel>
          </Tabs>
                  <Suspense fallback={<div></div>}>
                    <PoliciescontentInjectionpathsAdd loadGrid={policies_content_injection_pathsloadGrid} doClose={() => {closeAdd(idPrefix);}} idPrefix={idPrefix} 
            updatepolicies_content_injection_paths_paths={updatepolicies_content_injection_paths_paths} policies_content_injection_paths_paths={policies_content_injection_paths_paths}
            policies_content_injection_paths_paths_valid={policies_content_injection_paths_paths_valid}
            policies_content_injection_paths_paths_invalid_text={policies_content_injection_paths_paths_invalid_text}
                    />
                  </Suspense>
                  <Suspense fallback={<div></div>}>
                    <PoliciescontentInjectionpathsEdit  loadGrid={policies_content_injection_pathsloadGrid} dataLoad={dataLoad} name={"policies_content_injection_paths"} idPrefix={idPrefix + "_add"} parentIdPrefix={idPrefix} 
            updatepolicies_content_injection_paths_paths={updatepolicies_content_injection_paths_paths} policies_content_injection_paths_paths={policies_content_injection_paths_paths}
            policies_content_injection_paths_paths_valid={policies_content_injection_paths_paths_valid}
            policies_content_injection_paths_paths_invalid_text={policies_content_injection_paths_paths_invalid_text}
                    />
                  </Suspense>
        </div>
        </div>
    );
}

export default Policies_content_injectionAdd;
